

































































import { Component, Vue, Watch } from 'vue-property-decorator';
import {Divider,Row, Col} from 'vant';
import 'vant/lib/divider/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';

Vue.use(Divider).use(Row).use(Col);

@Component
export default class Process extends Vue {
    private bgColor = '#DFF4EF';
    private choice = '';

    @Watch('choice', {immediate: true, deep: true})
    public onChange(val: string) {
        if (val == '授权业务') {
            this.bgColor = '#DFF4EF';
        } else {
            this.bgColor = '#EED1D1';
        }
    }

  public created(): void {
        this.choice = this.$route.params.choice;
        this.$store.state.title = this.$t('business.process');
  }
}
